import React from 'react';
import foodplaceLogo from "~/assets/images/foodplaceLogo1.png";
import intranetBanner from "~/assets/images/intranet-page-bg.png";
import {
  DEFAULT_LOCATION_ID,
  SECTION_1_ID,
  SECTION_2_ID,
  SECTION_3_ID,
  SECTION_4_ID,
  EMPTYALLERGENSDATA_ENG,
  EMPTYALLERGENSDATA_DK,
  DIETARY_PREFERENCES_ENG,
  DIETARY_PREFERENCES_DK
} from '~/utils/constants';
import { json } from '@remix-run/node'
import { getDirectusClient } from '~/lib/directus'
import {
  format,
  startOfWeek,
  endOfWeek,
  getWeek,
  lastDayOfWeek,
  addDays,
  getDay
} from 'date-fns'
import { useLoaderData } from '@remix-run/react';
import Custom404Page from "~/routes/$";
import { getAssetURL } from "~/utils/get-asset-url";

export const loader = async ({ request, params }) => {
  const id = params.id;
  const lang = params.language;
  const presentTime = format(new Date(), "HH:mm")?.split(':')
  const presentWeek = (getDay(new Date()) == 6 && (Number(presentTime[0]) >= 1)) ? 7 : 0;
  const start = format(addDays(startOfWeek(new Date(), { weekStartsOn: 1 }), presentWeek), "yyyy-MM-dd");
  const end = format(addDays(endOfWeek(new Date(), { weekStartsOn: 6 }), presentWeek), "yyyy-MM-dd");
  const locationId = params.location ? params.location : DEFAULT_LOCATION_ID;
  const directus = await getDirectusClient();

  const response = await directus.items("screen_display").readByQuery({
    fields: ['*.*', 'recipe_id.allergens_list.Allergens_id.*', 'recipe_id.dietary_list.Dietary_id.*', "station_name.parent_id.*"],
    filter: {
      date: { _between: [start, end] }, location_id: { _eq: locationId },
      // station_name:{is_parent_station:{_eq:false}}
    },
  });
  const location_data = await directus.items(`Locations`).readByQuery({
    filter: {
      id: { _eq: locationId }
    },
    fields: ['*.*.*'],
  })
  const Locations = await directus.items('Locations').readByQuery({ fields: ['id'] })
  const LocationMatch = Locations?.data?.some(e => e?.id == locationId);

  return json({
    id: locationId,
    logoID: location_data?.data.length > 0 ? location_data?.data[0]?.logo?.id : null || null,
    data: response,
    LocationMatch,
    presentWeek
  });
};

export default function index() {
  const datas = useLoaderData()
  const englishCon = React.useRef(null);
  const danishCon = React.useRef(null);
  const { logoID, data, LocationMatch, presentWeek } = datas
  var startDate = addDays(startOfWeek(new Date(), { weekStartsOn: 1 }), presentWeek)
  var endDate = addDays(lastDayOfWeek(new Date(), { weekStartsOn: 6 }), presentWeek)
  const titleFilteration = ({ id, dateData, language, isChild }) => {
    const data = dateData?.filter((item) => {
      return (Number(item?.station_name?.station_unique_id) === Number(id))
    })[0]?.station_name
    return isChild ? data?.parent_id?.station_name : data?.station_name
  }

  const titleImage = ({ id, dateData, language, isChild }) => {
    const data = dateData?.filter((item) => {
      return (Number(item?.station_name?.station_unique_id) === Number(id))
    })[0]?.station_name
    return isChild ? data?.parent_id?.station_image_2 : data?.station_image_2
  }

  const contentFilteration = ({ id, dateData, language }) => {

    const data = dateData?.filter((item) => {
      return (Number(item?.station_name?.station_unique_id) === Number(id))
    })[0]
    return language === "da" ? data?.recipe_id?.menu_info?.trim() : data?.recipe_id?.menu_info_1?.trim()
  }

  const allergensFilteration = ({ id, dateData, language }) => {
    const data = dateData?.filter((item) => {
      return (Number(item?.station_name?.station_unique_id) === Number(id))
    })[0]
    const list = data?.recipe_id?.allergens_list?.map((e, i) => {
      const values = e?.Allergens_id
      return language === "da" ? (i !== 0 ? (`, ${values?.name}`) : values?.name) : (i !== 0 ? (`, ${values?.translated_name}`) : values?.translated_name)
    })
    return list
  }
  const dietaryFilteration = ({ id, dateData, language }) => {
    const data = dateData?.filter((item) => {
      return (Number(item?.station_name?.station_unique_id) === Number(id))
    })[0]
    const list = data?.recipe_id?.dietary_list?.map((e, i) => {
      const values = e?.Dietary_id
      return language === "da" ? (i !== 0 ? (`, ${values?.name}`) : values?.name) : (i !== 0 ? (`, ${values?.translated_name}`) : values?.translated_name)
    })
    return list
  }

  const isDataPresent = ({ id, dateData, language }) => {
    var flag = false
    const content = dateData?.filter((item) => {
      return (Number(item?.station_name?.station_unique_id) === Number(id))
    })[0]

    flag = language === "da" ? content?.recipe_id?.menu_info?.trim()?.length ? true : false : content?.recipe_id?.menu_info_1?.trim()?.length ? true : false
    return flag
  }

  const isAllergenDataPresent = ({ id, dateData, language }) => {
    var flag = false
    const allergensData = dateData?.filter((item) => {
      return (Number(item?.station_name?.station_unique_id) === Number(id))
    })[0]
    flag = allergensData?.recipe_id?.allergens_list?.length ? true : false
    return flag ? <><span className='font-bold'>{language === "da" ? "Allergener" : "Allergens"}: </span>
      {allergensFilteration({ dateData: dateData, id: id, language: language })}</> :
      <><span className='font-bold'>{language === "da" ? "Allergener" : "Allergens"}: </span>
        {language === "da" ? EMPTYALLERGENSDATA_DK : EMPTYALLERGENSDATA_ENG}</>
  }

  const isDietaryDataPresent = ({ id, dateData, language }) => {
    var flag = false
    const allergensData = dateData?.filter((item) => {
      return (Number(item?.station_name?.station_unique_id) === Number(id))
    })[0]
    flag = allergensData?.recipe_id?.dietary_list?.length ? true : false
    return flag ? <> <span className='font-bold'>{language === "da" ? DIETARY_PREFERENCES_DK : DIETARY_PREFERENCES_ENG}: </span>
      {dietaryFilteration({ dateData: dateData, id: id, language: language })} </> :
      null
  }
  const stationBody = ({ data, lang, id, styleChange }) => {
    return <>
      <div className={`content pt-2 ${styleChange ? "pt-6 lg:pt-9" : "pb-3"}`}>{contentFilteration({ dateData: data, id: id, language: lang })}</div>
      <div className='leading-normal allergen'>{isAllergenDataPresent({ dateData: data, id: id, language: lang })}</div>
      <div className='leading-normal dietary'>{isDietaryDataPresent({ dateData: data, id: id, language: lang })}</div>
    </>
  }
  const renderSingleStation = ({ data, lang, id }) => {
    return isDataPresent({ dateData: data, id: id, language: lang }) && <div className='col-span-12 md:col-span-4 f-16 p-2 text-velux_green'>
      <div className='font-bold md:min-h-[60px] pb-2 md:pb-0 uppercase inline-flex items-end station-title'>
        {titleImage({ dateData: data, id: id, language: lang, isChild: false }) ?
          <img src={getAssetURL(titleImage({ dateData: data, id: id, language: lang, isChild: false }))} width={id === 1 ||id === 12 ? "70px" : id === 5?"120px":"90px"} height={id === 1 ||id === 12 ? "70px" : "80px"} />
          : titleFilteration({ dateData: data, id: id, language: lang, isChild: false })
        }
      </div>
      {stationBody({ data: data, lang: lang, id: id, styleChange: false })}
    </div>
  }
  const dayMenu = (props) => {
    const pickerLang = {
      DaysEng: [
        'MONDAY',
        'TUESDAY',
        'WEDNESDAY',
        'THURSDAY	',
        'FRIDAY',
      ],
      DaysDa: [
        'MANDAG',
        'TIRSDAG',
        'ONSDAG',
        'TORSDAG',
        'FREDAG',
      ],
      from: 'From',
      to: 'To',
    }
    const DayMenu = []
    const DayFlag = []
    while (startDate <= endDate) {
      var dateData = data?.data?.filter((info) => {
        return (
          info?.date == format(startDate, "yyyy-MM-dd")
        )
      })
      if (dateData?.length > 0) {
        var flagValue = false;
        dateData?.map(e => {
          if (props === "da") {
            flagValue = flagValue === false ? (e?.recipe_id?.menu_info?.length ? true : false) : true
          }
          else {

            flagValue = flagValue === false ? (e?.recipe_id?.menu_info_1?.length ? true : false) : true
          }
        });
        DayFlag.push(flagValue)
        const IsdataPresent = [SECTION_1_ID, SECTION_2_ID,SECTION_3_ID,SECTION_4_ID]?.map(e => isDataPresent({ dateData: dateData, id: e, language: props }));
        DayMenu.push(
          <>
            {flagValue &&

              <div key={pickerLang.DaysDa[(startDate).getDay() - 1]} className="content-section xs:px-[15px] md:p-6 lg:p-10">
                <div className='block days py-[25px]' key={pickerLang.DaysDa[(startDate).getDay() - 1]}>
                  <div className='block title text-velux_green font-bold uppercase text-2xl tracking-[0.6px] text-center p-4'>
                    {props === "da" ? pickerLang.DaysDa[(startDate).getDay() - 1] : pickerLang.DaysEng[(startDate).getDay() - 1]}</div>
                  {IsdataPresent?.includes(true) &&
                    <>
                      <div className="grid grid-cols-12 gap-4 gap-y-[30px] md:gap-y-[60px] py-4 md:py-8">
                        {[SECTION_1_ID, SECTION_2_ID,SECTION_3_ID,SECTION_4_ID]?.map(e => renderSingleStation({ data: dateData, lang: props, id: e }))}
                      </div>
                    </>}
                </div>

              </div>
            }
          </>
        )
      }
      startDate = addDays(startDate, 1)

    }
    startDate = startOfWeek(new Date(), { weekStartsOn: 1 })
    return { DayMenu: DayMenu, flagValue: DayFlag }
  }
  const handleScroll = (ref) => {
    ref.current.scrollIntoView({ behavior: "smooth", block: "start" })
  };
  const Body = (props) => {
    return (
      <>
        {dayMenu(props)?.flagValue?.includes(true) && <div className='main-title flex items-center justify-between leading-normal relative'
          ref={(props === "en") ? englishCon : danishCon}
        >
          <div className='intranet-banner-image'>
            <img src={intranetBanner} alt='intranet banner' className='min-h-[100px]' />
          </div>
          <div className='overlay-images absolute top-0 flex justify-between items-center w-full h-full px-4 md:px-10 py-[10px]'>
            <div className='flex max-w-[65px] sm:max-w-[80px] lg:max-w-[100px]'>
              <img src={foodplaceLogo} alt="food-place-logo" className="max-w-full" />

            </div>
            <div className='flex flex-col title text-white text-center leading-normal my-1 '>
              <div className='text-base md:text-lg font-normal'>{props === "da" ? "UGE" : "Week"} {getWeek(addDays(new Date(), presentWeek))} </div>
              <div className="font-bold text-[20px] md:text-[24px]">{props === "da" ? "Ugens menu" : "Menu of the week"}</div>
            </div>
            <div className='flex items-center'>
              <div className={`lang f-18 tracking-[0.6px] px-1 md:px-2 leading-[16px] text-white cursor-pointer ${props === "da" ? ' font-bold' : ''}`}
                onClick={() => handleScroll(danishCon)}
              >DK</div>
              <div className='px-1 md:px-2 relative'><span className='v-line'></span></div>
              <div className={`lang f-18 tracking-[0.6px] px-1 md:px-2 leading-[16px] text-white cursor-pointer ${props === "en" ? ' font-bold' : ''}`}
                onClick={() => handleScroll(englishCon)}
              >EN</div  >
            </div>
          </div>
        </div>}
        <div className='weekdays'>
          {dayMenu(props)?.flagValue?.includes(true) && <div className=''>
            {dayMenu(props)?.DayMenu}
          </div>}
        </div>

      </>)
  }

  return (
    <> {LocationMatch === false ? <Custom404Page /> : <>
      <div className="menu-of-the-week font-noto">
        <div className='container-width m-auto'>
          {Body('da')}
          {Body('en')}
        </div>
      </div>

    </>}
    </>
  )
}
